//  --sb-cp-full-page-loader-margin-bottom - Image margin bottom
//  --sb-cp-full-page-loader_image-height - Image height
//  --sb-cp-full-page-loader_image-max-height - Image max height
//  --sb-cp-full-page-loader-header-font-size - Header font size
//  --sb-cp-full-page-loader-subheader-font-size - Subheader font size

.sb-cp-loader_body {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
}

.sb-cp-loader_image {
  animation: sb-bounce 3s cubic-bezier(0.28, 0.84, 0.42, 1) infinite;
  height: var(--sb-cp-full-page-loader_image-height, 250px);
  margin-bottom: var(
    --sb-cp-full-page-loader-margin-bottom,
    var(--sbt-spacing-100)
  );
  max-height: var(--sb-cp-full-page-loader_image-max-height, 250px);
}

.sb-cp-loader_header {
  color: var(--sbt-theme-color-70);
  font-size: var(
    --sb-cp-full-page-loader-header-font-size,
    var(--sbt-font-size-90)
  );
}

.sb-cp-loader_subheader {
  color: var(--sbt-theme-tertiary-color-40);
  font-size: var(
    --sb-cp-full-page-loader-subheader-font-size,
    var(--sbt-font-size-70)
  );
}
