.sb-cp-searchbar__select {
  flex-basis: var(--sb-cp-search-template-select-width, 175px);
}

.sb-cp-searchbar {
  display: flex;
  gap: var(--sbt-spacing-30);
}

.sb-cp-searchbar__input-group {
  --sbt-color-disabled-border: var(--sbt-color-disabled-background);
  width: 100%;

  .sb-input-group__item.sb-cp-searchbar__query {
    flex: 1;
  }
}

.sb-cp-searchbar__input-group.sb-input-group__vertical {
  --sb-cp-search-template-select-width: 100%;
}
