.sb-cp-journey-header__content {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: space-between;
}

.sb-cp-journey-header__name {
  margin-right: var(--sbt-spacing-20);
  width: 25rem;
}

.sb-cp-journey-header__back-button {
  display: inline-block;
  font-size: var(--sb-typography-T30-font-size, var(--sbt-font-size-30));
  font-weight: var(--sb-typography-bold, var(--sbt-font-weight-70));
  margin-left: var(--sbt-spacing-10);

  &.sb-button_auto.sb-button_link {
    margin: 0.5rem;
  }
}

.sb-cp-journey-header {
  display: grid;
  grid-auto-columns: max-content;
  grid-template-areas: 'left right';
  grid-template-columns: auto;
  padding: 0.25rem 0;
}

.sb-cp-journey-header__left-region {
  align-items: center;
  display: inline-flex;
  grid-area: left;
}

.sb-cp-journey-header__right-region {
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: var(--sbt-spacing-10);
  justify-content: flex-end;

  &.sb-cp-action-area__area-light {
    color: var(--sbt-white);
  }

  &.sb-cp-action-area__area-dark {
    color: var(--sbt-gray-80);
  }
}

.sb-cp-journey-header__copilot-icon {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 0;
}

.sb-cp-journey-header__popover {
  --sb-button-color: var(--sbt-black);
  align-items: center;
  background-color: var(--sbt-gray-10);
  border: none;
  display: flex;
  justify-content: center;

  &:hover {
    cursor: pointer;
  }
}

.sb-cp-journey-header__copilot {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  transition: all 0.2s ease-in-out;

  &:hover {
    cursor: pointer;
  }
}

.sb-cp-journey-header__arrow {
  --sb-icon__color: var(--sbt-gray-30);
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  transition: all 0.2s ease-in-out;

  &:hover {
    cursor: pointer;
  }
}

.sb-cp-journey-header__separator {
  border-left: 1px solid;
  color: var(--sbt-gray-20);
  height: 100%;
  margin: 0 var(--sbt-spacing-30);
}

/* stylelint-disable selector-class-pattern -- Third Party Classes */
:global(.tippy-content) {
  padding: 0;
}
