// SB Button Variables:
// --sb-input-group-border-color - Border color
// --sb-input-group-outer-width - Outer border widths
// --sb-input-group-inner-width - Inner border widths

.sb-input-group {
  --sb-input-group-border-color: var(--sbt-theme-tertiary-color-30);
  --sb-input-group-inner-width: 1px;
  --sb-input-group-outer-width: 1px;

  --sbt-input-border-color: transparent;
  --sb-button-border-radius: 0;
  --sbt-input-border-radius: 0;
  border-radius: var(--sbt-border-radius-large);
  display: inline-flex;
  flex-direction: row;

  .sb-input-group__item {
    border-bottom-width: var(--sb-input-group-outer-width);
    border-color: var(--sb-input-group-border-color)
      var(--sb-input-group-border-color) var(--sb-input-group-border-color)
      transparent;
    border-left-width: var(--sb-input-group-inner-width);
    border-right-width: var(--sb-input-group-inner-width);
    border-style: solid;
    border-top-width: var(--sb-input-group-outer-width);

    &:first-child {
      --sbt-input-border-radius: var(--sbt-border-radius-large) 0 0
        var(--sbt-border-radius-large);
      --sb-button-border-radius: var(--sbt-border-radius-large) 0 0
        var(--sbt-border-radius-large);
      border-left-color: var(--sb-input-group-border-color);
      border-left-width: var(--sb-input-group-outer-width);
      border-radius: var(--sbt-border-radius-large) 0 0
        var(--sbt-border-radius-large);
    }

    &:last-child {
      --sbt-input-border-radius: 0 var(--sbt-border-radius-large)
        var(--sbt-border-radius-large) 0;
      --sb-button-border-radius: 0 var(--sbt-border-radius-large)
        var(--sbt-border-radius-large) 0;
      border-radius: 0 var(--sbt-border-radius-large)
        var(--sbt-border-radius-large) 0;
      border-right-width: var(--sb-input-group-outer-width);
    }
  }

  .sb-input-group__item.sb-input-group__item-button {
    border-bottom-width: 0;
    border-left-width: 0;
    border-right-width: 0;
    border-top-width: 0;
  }

  &:focus-within {
    --sbt-input-box-shadow: none;
    box-shadow: var(--sbt-elevation-focus);

    .sb-input-group__item {
      border-color: transparent var(--sb-input-group-border-color) transparent
        transparent;

      &:first-child {
        border-left-color: transparent;
      }

      &:last-child {
        border-right-color: transparent;
      }
    }
  }
}

.sb-input-group__vertical {
  flex-direction: column;

  .sb-input-group__item {
    border-color: var(--sb-input-group-border-color)
      var(--sb-input-group-border-color) transparent
      var(--sb-input-group-border-color);
    flex-basis: 100%;

    &:first-child {
      --sbt-input-border-radius: var(--sbt-border-radius-large)
        var(--sbt-border-radius-large) 0 0;
      --sb-button-border-radius: var(--sbt-border-radius-large)
        var(--sbt-border-radius-large) 0 0;
      border-radius: var(--sbt-border-radius-large)
        var(--sbt-border-radius-large) 0 0;
    }

    &:last-child {
      --sbt-input-border-radius: 0 0 var(--sbt-border-radius-large)
        var(--sbt-border-radius-large);
      --sb-button-border-radius: 0 0 var(--sbt-border-radius-large)
        var(--sbt-border-radius-large);
      border-bottom-color: var(--sb-input-group-border-color);
      border-radius: 0 0 var(--sbt-border-radius-large)
        var(--sbt-border-radius-large);
    }
  }

  .sb-input-group__item-button {
    border-width: 0;
  }

  &:focus-within {
    --sbt-input-box-shadow: none;
    box-shadow: var(--sbt-elevation-focus);

    .sb-input-group__item {
      border-color: var(--sb-input-group-border-color) transparent transparent
        transparent;

      &:first-child {
        border-top-color: transparent;
      }
    }
  }
}
