.sb-cp-search-surface__dual-panel-layout {
  --sb-panel-layout-medium-columns: 20fr 80fr;
}

.sb-cp-search-surface__hint {
  --sb-typography-color: var(--sbt-theme-tertiary-color-40);

  font-weight: var(--sbt-font-weight-60);
  left: 50%;
  position: absolute;
  text-align: center;
  top: calc(50% + 20px);
  transform: translate(-50%, -50%);
  width: 100%;
}
