/* stylelint-disable selector-class-pattern -- Third Party Classes */
.sb-cp-popover-template__wrapper {
  background-color: var(--sbt-theme-tertiary-color-70);
  border-radius: var(--sbt-border-radius-large);

  .tippy-content {
    margin: 0;
    padding: 0;
  }

  .tippy-arrow::before {
    color: var(--sbt-theme-tertiary-color-70);
  }
}

.sb-cp-popover-template__header {
  align-items: center;
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-60);
  color: var(--sbt-theme-tertiary-color-10);
  display: flex;
  justify-content: space-between;
  padding: var(--sbt-spacing-30) var(--sbt-spacing-40);
}

.sb-cp-popover-template__close-button {
  --sb-button-color: var(--sbt-theme-tertiary-color-60);
  --sb-button-color-dark: var(--sbt-theme-tertiary-color-10);
  --sb-button-font-size: var(--sbt-font-size-20);
  --sb-button-icon-only-width: var(--sbt-spacing-40);
  margin: 0;
}

.sb-cp-popover-template__content {
  padding: var(--sbt-spacing-20) 0;

  [aria-expanded='true'] a {
    --sb-typography-color: var(--sbt-theme-tertiary-color-10);
  }
}
