.sb-standard-table_cell-id {
  background-color: var(--sb-multi-value-background-color);
  border: 1px solid var(--sbt-input-border-color);
  border-radius: var(--sbt-border-radius-large);
  color: var(--sb-multivalue-text-color);
  font-size: var(--sb-input-text-size);
  font-weight: var(--sb-input-text-weight);
  line-height: var(--sbt-line-height-25);
  overflow: hidden;
  padding: 3px 6px;
  text-overflow: ellipsis;
}
