.sb-cp-title-header-template {
  align-items: center;
  display: flex;
  flex-direction: row;
  height: 100%;
  justify-content: flex-start;
}

.sb-cp-title-header-template__content {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}

.sb-cp-title-header-template__title {
  align-items: center;
  display: flex;
}

.sb-cp-title-header-template__name {
  margin-right: var(--sbt-spacing-20);
}

.sb-cp-title-header-template__badge {
  --sb-badge-size: 20px;
  font-size: var(--sbt-font-size-15);
  font-weight: var(--sbt-font-weight-50);
}

.sb-cp-title-header-template__subtitle {
  --sb-typography-T15-line-height: var(--sbt-line-height-ui);
  --sb-typography-color: var(--sbt-theme-tertiary-color-50);
  color: var(--sbt-theme-tertiary-color-50);
}

.sb-cp-title-header-template__icon {
  --sb-icon_padding: 1px;
  --sb-icon__size: 1rem;
  margin-right: var(--sbt-spacing-10);
}

.sb-cp-title-header-template__description {
  display: inline-block;
}

.sb-cp-title-header-template__back {
  --sb-typography-color: var(--sbt-theme-color-70);
  --sb-icon__color: var(--sbt-theme-color-70);
  cursor: pointer;
  margin-right: var(--sbt-spacing-50);

  &:hover {
    --sb-typography-color: var(--sbt-theme-color-80);
    --sb-icon__color: var(--sbt-theme-color-80);
  }
}

.sb-cp-title-header-template__back-text {
  display: inline-block;
  margin-left: var(--sbt-spacing-10);
}
