.sb-cp-popover-template-item__popover-wrapper {
  background-color: var(--sbt-theme-tertiary-color-70);
  border-radius: var(--sbt-border-radius-large);
  box-shadow: var(--sbt-elevation-40);
  padding: var(--sbt-spacing-20) 0;
}

.sb-cp-popover-template-item-link {
  --sb-icon__size: var(--sbt-font-size-20);
  --sb-link__size: var(--sbt-font-size-20);
  --sb-typography-color: var(--sbt-theme-tertiary-color-30);
  --sb-typography-10-line-height: var(--sbt-line-height-ui);
  cursor: pointer;
  display: flex;
  padding: var(--sbt-spacing-30) var(--sbt-spacing-40);
  width: max-content;

  &:hover {
    --sb-typography-color: var(--sbt-theme-tertiary-color-10);
  }
}

.sb-cp-popover-template-item-link_disabled {
  --sb-typography-color: var(--sbt-theme-tertiary-color-50);

  &:hover {
    --sb-typography-color: var(--sbt-theme-tertiary-color-50);
  }
}

.sb-cp-popover-template-item-link__left-icon {
  --sb-icon__color: var(--sbt-theme-tertiary-color-60);
  --sb-icon__size: var(--sbt-font-size-30);
  height: var(--sbt-font-size-20);
  margin-left: var(--sbt-spacing-20);
  margin-right: var(--sbt-spacing-30);
  margin-top: -4px;
}

.sb-cp-popover-template-item-link__right {
  --sb-typography-T10-line-height: var(--sbt-line-height-ui);
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.sb-cp-popover-template-item-link__right-icon {
  --sb-icon__color: var(--sbt-theme-tertiary-color-30);
  --sb-icon_padding: 0;
  line-height: var(--sbt-line-height-ui);
  margin-left: var(--sbt-spacing-30);
}
