.sb-danger-button {
  --sb-button-color: var(--sbt-color-error-50);
  --sb-button-color-dark: var(--sbt-color-error-50);
  border-color: var(--sbt-theme-tertiary-color-30);

  &:hover {
    background-color: var(--sbt-theme-tertiary-color-10);
    border-color: var(--sbt-theme-tertiary-color-50);
  }
}
