/* stylelint-disable selector-class-pattern -- Third Party Classes */
.sb-cp-action-button__popover .tippy-content {
  padding: 0;
}

.sb-cp-action-button__button {
  --sb-button-padding-horizontal-smaller: 0.5rem;
  --sb-button-text-padding: 0 0.33rem 0 0;
}

.sb-cp-action-box {
  .sb-select
    .sb-select__option--is-focused:not(.sb-select__option--is-disabled) {
    background: transparent;

    &:hover,
    &:active {
      background-color: var(--sbt-theme-color-20);
    }
  }
}
