/* stylelint-disable selector-class-pattern -- Third Party Classes */
.sb-data-grid {
  --sb-table-padding-cell-side: 0.75rem;
  --sb-table-color-border: var(--sbt-theme-tertiary-color-20);
  --sb-table-color-text: var(--sbt-theme-tertiary-color-80);
  --sb-table-header-color-background: var(--sbt-theme-tertiary-color-20);
  --sb-table-header-active-color-background: var(--sbt-theme-color-30);
  --sb-table-striped-row-even-color-background: var(
    --sbt-theme-tertiary-color-10
  );
  --sb-table-header-cell-font-size: var(--sbt-font-size-20);
  --sb-table-font-size: var(--sbt-font-size-20);
  --sb-table-cell-background-color: transparent;
  height: 100%;

  .sb-checkbox-header,
  .sb-checkbox-cell {
    justify-content: center;
    padding: var(--sb-table-cell-padding, 0 0.5rem);

    .ag-cell-wrapper {
      justify-content: center;
    }

    .ag-selection-checkbox.ag-selection-checkbox,
    .ag-checkbox.ag-checkbox {
      margin: 0;
    }

    .ag-cell-label-container {
      display: none;
    }
  }

  .sb-checkbox-grid {
    .ag-input-field-input.ag-checkbox-input {
      appearance: auto;
      height: auto;
      margin: 2px 3px 3px 7px;
      opacity: 1;
      width: auto;
    }

    .ag-cell-focus[col-id='checkbox'] {
      border: none !important;
    }

    .ag-checkbox-input-wrapper::after,
    .ag-checkbox-input-wrapper.ag-checked::after {
      content: none;
    }

    .ag-checkbox-input-wrapper:focus-within,
    .ag-checkbox-input-wrapper:active {
      box-shadow: none;
    }

    .ag-wrapper.ag-input-wrapper.ag-checkbox-input-wrapper {
      background-color: transparent;
    }
  }

  .ag-header-cell.sb-checkbox-header {
    .ag-header-cell-comp-wrapper {
      display: none;
    }
  }

  .ag-cell.sb-checkbox-cell.sb-checkbox-cell {
    .ag-cell-value {
      display: none;
    }
  }
}

.sb-data-grid_bordered {
  --sb-table-row-border: 0;
}

.sb-data-grid_borderless {
  --sb-table-border: 0;
  --sb-table-cell-border: 0;
  --sb-table-row-bottom-border: 0;
}

.sb-data-grid_bordered-vertical {
  --sb-table-border: 0;
  --sb-table-cell-border: 0;
  --sb-table-cell-right-border: 1px solid;
  --sb-table-row-border: 0;

  .ag-cell:last-child {
    --sb-table-cell-right-border: 0;
  }
}

.sb-data-grid_bordered-horizontal {
  --sb-table-border: 0;
  --sb-table-cell-border: 0;
  --sb-table-row-bottom-border: 1px solid;
}

.sb-data-grid_bordered-rows {
  --sb-table-border: 0;
  --sb-table-cell-border: 0;
  --sb-table-row-bottom-border: 1px solid;

  .ag-cell:first-child {
    --sb-table-cell-left-border: 1px solid;
  }

  .ag-cell:last-child {
    --sb-table-cell-right-border: 1px solid;
  }

  &.sb-data-grid-row-space {
    .ag-cell:first-child {
      --sb-table-cell-left-border: 0;
    }

    .ag-cell:last-child {
      --sb-table-cell-right-border: 0;
    }
  }
}

.sb-data-grid_stripeless {
  --sb-table-striped-row-even-color-background: var(
    --sb-table-cell-background-color
  );
}

.sb-data-grid-row-space .ag-row {
  --sb-table-row-border: 1px solid;
  border-radius: 0.5rem;
  margin: var(--sbt-spacing-20) 0;
}

.sb-data-grid-row-space .ag-row:not(.ag-row-level-1) {
  max-height: 48px;
}

.sb-data-grid-size_large.sb-data-grid-row-space .ag-row:not(.ag-row-level-1) {
  max-height: 56px;
}

.sb-data-grid-grouped {
  .ag-row-level-0.ag-row-level-0 {
    // --sbt-gray-10 wasn't light enough so I had to use a custom color to match tge design for now.
    background-color: var(
      --sb-datagrid-group-header-background,
      var(--sbt-theme-tertiary-color-10)
    );
  }
}

.sb-data-grid-grouped {
  .ag-row-level-0.ag-row-level-0 {
    font-size: var(--sbt-font-size-25);
    font-weight: var(--sbt-font-weight-60);
  }

  .ag-row-level-1.ag-row-level-1 {
    font-size: var(--sbt-font-size-25);
  }
}

.sb-data-grid-master-detail {
  &.sb-data-grid-row-space .ag-row-position-absolute.ag-row.ag-row-level-1 {
    margin: 0;
  }

  .ag-cell-wrapper.ag-cell-expandable.ag-row-group.ag-row-group-indent-0 {
    align-items: center;
  }

  .ag-row-position-absolute.ag-row.ag-row-level-1 {
    border-radius: 0 0 0.5rem 0.5rem;
    border-top: 0;
    max-height: initial;
    // We reset this value since Ag Grid sets it to `no-wrap` and for Master/Detail introduces issues (ex: a paragraph tag won't wrap its text and will take the full width).
    white-space: initial;
  }

  .ag-row.ag-row-group-expanded {
    border-bottom: 0;
    border-radius: 0.5rem 0.5rem 0 0;
  }
}
