.sb-cp-entity-header-template {
  padding: var(--sbt-spacing-40) 0;
}

.sb-cp-entity-header-template__row {
  align-items: center;
  display: flex;
  justify-content: space-between;

  > *:first-child,
  .sb-cp-entity-header-template__title {
    flex: 1;
  }

  .sb-cp-entity-header-template__title {
    align-items: center;
    display: flex;
    justify-content: left;

    > * {
      padding-right: var(--sbt-spacing-30);
    }
  }
}

.sb-cp-entity-header-template__breadcrumbs-row {
  padding-bottom: var(--sbt-spacing-20);
}

.sb-cp-entity-header-template__title-row,
.sb-cp-entity-header-template__subtext-row {
  padding-bottom: var(--sbt-spacing-10);
}

.sb-cp-entity-header-template__breadcrumbs > .sb-breadcrumbs > * {
  display: inline-block;
}

.sb-cp-entity-header-template__options > .sb-button {
  font-size: var(--sbt-font-size-25);
  margin-left: var(--sbt-spacing-30);
}

.sb-cp-entity-header-template__chips > * {
  margin-right: var(--sbt-spacing-20);
}

.sb-cp-entity-header-template__meta,
.sb-cp-entity-header-template__subtext {
  color: (var(--sbt-theme-tertiary-color-70));
}

.sb-cp-entity-header-template__subtext {
  line-height: var(--sbt-spacing-50);
}
