@import '../../../../styles/breakpoints';

.sb-cp-side-nav {
  .sb-cp-side-nav-item {
    padding: var(--sbt-spacing-20) 0;
  }

  .sb-cp-side-nav-item__container {
    border-right: 3px solid transparent;
    display: flex;
    flex-direction: column;
    padding-left: var(--sbt-spacing-40);
    padding-right: calc(var(--sbt-spacing-40) - 3px);
  }

  .sb-cp-side-nav-item__container_is-selected {
    border-right: 3px solid var(--sbt-theme-color-70);
  }

  .sb-cp-side-nav-item__link {
    border-bottom: none;
    text-align: center;
  }

  .sb-cp-side-nav-item__icon {
    border-radius: var(--sb-button-border-radius);
    font-size: 16px;
    height: 30px;
    padding: 7px;
    width: 32px;
  }

  .sb-cp-side-nav-item__icon_is-hoverable:hover,
  .sb-cp-side-nav-item__icon_is-selected {
    background-color: var(--sbt-theme-tertiary-color-20);
  }

  .sb-cp-side-nav-item__text {
    --sb-typography-color: var(--sbt-theme-tertiary-color-70);

    @include window-width-min-small {
      display: none;
    }
  }
}

@include window-width-max-small {
  .sb-cp-side-nav {
    .sb-cp-side-nav-item {
      padding: 0 var(--sbt-spacing-20);
    }

    .sb-cp-side-nav-item__container {
      border-bottom: 3px solid transparent;
      border-right: none;
      padding: var(--sbt-spacing-20) 0;
    }

    .sb-cp-side-nav-item__container_is-selected {
      border-bottom: 3px solid var(--sbt-theme-color-70);
      border-right: none;
    }
  }
}

.sb-cp-side-nav-item__visible-on-wide-up {
  @include window-width-max-wide {
    display: none;
  }
}

.sb-cp-side-nav-item__visible-on-large-up {
  @include window-width-max-large {
    display: none;
  }
}

.sb-cp-side-nav-item__visible-on-medium-up {
  @include window-width-max-medium {
    display: none;
  }
}

.sb-cp-side-nav-item__visible-on-small-up {
  @include window-width-max-small {
    display: none;
  }
}

.sb-cp-side-nav-item__visible-on-large-down {
  @include window-width-min-wide {
    display: none;
  }
}

.sb-cp-side-nav-item__visible-on-medium-down {
  @include window-width-min-large {
    display: none;
  }
}

.sb-cp-side-nav-item__visible-on-small-down {
  @include window-width-min-medium {
    display: none;
  }
}

.sb-cp-side-nav-item__visible-on-slim-down {
  @include window-width-min-small {
    display: none;
  }
}
