.sb-cp-journey-summary-status-bullet {
  border: 1px solid transparent;
  border-radius: 50%;
  height: 8px;
  margin: 0 0 0 var(--sbt-spacing-20);
  width: 8px;
}

.sb-cp-journey-summary-status-bullet__in-progress {
  background-color: var(--sbt-blue-70);
}

.sb-cp-journey-summary-status-bullet__paused {
  background-color: var(--sbt-grey-70);
}

.sb-cp-journey-summary-status-bullet__done {
  background-color: var(--sbt-green-70);
}

.sb-cp-journey-summary-status-bullet__late {
  background-color: var(--sbt-yellow-70);
}

.sb-cp-journey-summary-status-item {
  align-items: center;
  border: 1px solid;
  border-radius: var(--sbt-border-radius-large);
  display: flex;
  gap: var(--sbt-spacing-30);
  padding: var(--sbt-spacing-10);
}

.sb-cp-journey-summary-status-item__in-progress {
  border-color: var(--sbt-blue-70);
}

.sb-cp-journey-summary-status-item__paused {
  border-color: var(--sbt-grey-70);
}

.sb-cp-journey-summary-status-item__done {
  border-color: var(--sbt-green-70);
}

.sb-cp-journey-summary-status-item__late {
  border-color: var(--sbt-yellow-70);
}
