.sb-search {
  align-items: center;
  display: flex;
  width: 180px;
}

.sb-search__input-container {
  position: relative;
}

.sb-search__clear {
  --sb-button-color: var(--sbt-theme-tertiary-color-30);
  background-color: transparent;
  border: 0;
  display: block;
  height: 100%;
  padding: 0.125rem;
  position: absolute;
  right: calc(180px - 175px);
  top: 0;
  width: auto;

  &:hover {
    --sb-button-color: var(--sbt-theme-tertiary-color-50);
  }
}

.sb-search__icon {
  color: var(--sbt-theme-tertiary-color-50);
  left: 0.375rem;
  pointer-events: none;
  position: absolute;
  top: calc(50% - 0.5rem);
}

.sb-search__cancel {
  --sb-button-color: var(--sbt-theme-tertiary-color-50);
  margin-left: 5px;
  padding: 0;

  &:hover {
    --sb-button-color: var(--sbt-theme-tertiary-color-60);
    background: initial;
  }
}

.sb-search__field__input {
  padding-right: 1.625rem;

  &:focus ~ .sb-search__icon {
    color: var(--sbt-theme-tertiary-color-60);
  }
}

// variant: search with icon
.sb-search_with_icon {
  .sb-search__field__input {
    padding-left: 1.75rem;
  }
}

// variant: search in nav
.sb-search_in-nav {
  .sb-search__field__input {
    border: 0;
    font-size: var(--sbt-font-size-25);
    max-height: 2rem;
  }

  .sb-search__field__input:focus {
    background-color: var(--sbt-color-background);
    color: var(--sbt-theme-tertiary-color-90);

    &::placeholder,
    ~ .sb-search__icon {
      color: var(--sbt-theme-tertiary-color-30);
    }

    ~ .sb-search__clear {
      --sb-button-color: var(--sbt-theme-tertiary-color-20);

      &:hover {
        --sb-button-color: var(--sbt-theme-tertiary-color-50);
      }
    }

    ~ .sb-search__cancel {
      --sb-button-color: var(--sbt-theme-tertiary-color-20);

      &:hover {
        --sb-button-color: var(--sbt-theme-tertiary-color-50);
      }
    }
  }

  .sb-search__clear {
    top: 0;
  }
}

// variant: search in nav - dark
.sb-search_in-nav_dark {
  .sb-search__field__input {
    background-color: var(--sbt-color-header-darker);
    color: rgba(#fff, 0.75);

    &::placeholder {
      color: var(--sbt-theme-tertiary-color-60);
    }
  }

  .sb-search__clear {
    --sb-button-color: rgba(#fff, 1);
  }

  .sb-search__cancel {
    --sb-button-color: rgba(#fff, 1);
  }

  .sb-search__icon {
    color: rgba(#fff, 0.6);
  }
}

// variant: search in nav - light
.sb-search_in-nav_light {
  .sb-search__field__input {
    background-color: var(--sbt-color-header-lighter);
    color: var(--sbt-theme-tertiary-color-30);

    &::placeholder {
      color: var(--sbt-theme-tertiary-color-30);
    }
  }

  .sb-search__clear {
    --sb-button-color: var(--sbt-theme-tertiary-color-30);

    &:hover {
      --sb-button-color: var(--sbt-theme-tertiary-color-50);
    }
  }

  .sb-search__cancel {
    --sb-button-color: var(--sbt-theme-tertiary-color-30);

    &:hover {
      --sb-button-color: var(--sbt-theme-tertiary-color-50);
    }
  }

  .sb-search__icon {
    color: var(--sbt-theme-tertiary-color-20);
  }
}
