.sb-cp-search-results {
  background-color: var(--sbt-white);
  border: var(--sbt-input-border-size) solid var(--sbt-theme-tertiary-color-30);
  border-radius: var(--sbt-border-radius-small);
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  left: 0;
  position: absolute;
  right: 0;
  z-index: var(--sbt-stack-overlay);
}

.sb-cp-search-results-loader {
  padding: var(--sbt-spacing-10);
}

.sb-cp-search-result-footer {
  border-top: var(--sbt-input-border-size) solid
    var(--sb-input-group-border-color);
}

.sb-cp-search-result-footer-link {
  font-weight: var(--sbt-font-weight-40);
}

.sb-cp-search-result-header {
  --sb-typography-color: var(--sbt-color-disabled-text);
  border-bottom: var(--sbt-input-border-size) solid
    var(--sb-input-group-border-color);
  padding: var(--sbt-spacing-20) var(--sbt-spacing-40);
}
