.sb-cp-filter-bar-template {
  display: grid;
  grid-template-areas: 'left right';
  grid-template-columns: auto;
}

.sb-cp-filter-bar-template__main {
  display: flex;
  justify-content: space-between;
}

.sb-cp-filter-bar-template__left-region {
  display: flex;
  flex: 1;
  grid-area: left;
  margin: auto 0;
}

.sb-cp-filter-bar-template__right-region {
  display: flex;
  grid-area: right;
  justify-self: end;
  margin: auto 0;
}
