.sb-cp-standard-table__options-group .sb-cp-standard-table__option {
  --sb-button-color: var(--sbt-theme-tertiary-color-20);
  --sb-button-color-contrast: var(--sbt-theme-tertiary-color-60);
  padding: var(--sbt-spacing-10);

  &:hover:not(&:disabled) {
    --sb-button-color: var(--sbt-theme-tertiary-color-40);
  }
}

.sb-cp-standard-table__options-group .sb-cp-standard-table__options-menu {
  .sb-select
    .sb-select__option--is-focused:not(.sb-select__option--is-disabled) {
    background: transparent;

    &:hover,
    &:active {
      background-color: var(--sbt-theme-color-20);
    }
  }
}

.sb-cp-standard-table__options-group
  > .sb-cp-standard-table__option:first-child {
  margin-left: 0;

  .sb-cp-standard-table__option {
    margin-left: 0;
  }
}

.sb-cp-standard-table__options-group
  > .sb-cp-standard-table__option:last-child {
  margin-right: 0;

  .sb-cp-standard-table__option {
    margin-right: 0;
  }
}
