.sb-cp-journey-summary-icon-layout {
  align-items: center;
  display: flex;
  justify-content: space-evenly;
}

.sb-cp-journey-summary-columns {
  display: grid;
  flex-direction: column;
  gap: var(--sbt-spacing-10) var(--sbt-spacing-20);
  grid-auto-flow: column;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, 1fr);
}

.sb-cp-journey-summary-light-font {
  color: var(--sbt-gray-40);
  font-weight: var(--sbt-font-weight-40);
}

.sb-cp-journey-summary-parent-layout {
  align-items: center;
  border: 1px solid var(--sbt-gray-20);
  border-radius: var(--sbt-spacing-20);
  display: flex;
  flex-direction: row;
  gap: var(--sbt-spacing-40);
  justify-content: space-between;
  padding: var(--sbt-spacing-20);
}

.sb-cp-journey-summary-grid-layout {
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: var(--sbt-spacing-40);
  justify-content: space-around;
}

.sb-cp-journey-summary-stepper-row {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.sb-cp-journey-summary-title-row {
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: var(--sbt-spacing-20);
  justify-content: space-evenly;
}
