@import '../../../../styles/breakpoints';

.sb-cp-side-nav {
  --sb-icon__color: var(--sbt-black);
}

.sb-menu.sb-cp-side-nav__header-menu {
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-20);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 11px 0;
}

.sb-menu.sb-cp-side-nav__body-menu {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-x: hidden;
  padding: var(--sbt-spacing-20) 0;

  @include window-width-max-small {
    flex-direction: row;
    justify-content: space-evenly;
    max-width: 100vw;
    padding: 0;
    transition: transform 0.25s ease-in-out;
    width: 100%;
    z-index: var(--sbt-stack-floating);
  }
}

@include window-width-max-small {
  .sb-cp-side-nav__header {
    display: none;
  }
}
