.sb-cp-journey-summary-type-row {
  display: grid;
  gap: var(--sbt-spacing-10);
  grid-template-columns: repeat(2, 1fr);
  margin: var(--sbt-spacing-10);
}

.sb-cp-journey-summary-type-title {
  align-items: center;
  display: flex;
  gap: var(--sbt-spacing-30);
  padding: var(--sbt-spacing-10);
}
