.sb-cp-standard-table_header {
  align-items: center;
  background-color: transparent;
  display: flex;
  height: 100%;
  width: 100%;

  .sb-cp-standard-table_header-icon {
    margin: 0 var(--sbt-spacing-10);
  }

  .sb-cp-standard-table_header-badge {
    --sb-badged-container-top-right-translate-x: 10%;
    --sb-badged-container-top-right-translate-y: -10%;
    //background-color: var(--sbt-theme-tertiary-color-10);
    --sb-icon__color: var(--sbt-theme-color-60);
    cursor: pointer;
    padding: 3px;

    &:hover {
      --sb-icon__color: var(--sbt-theme-color-80);
      //background-color: var(--sbt-theme-tertiary-color-20);
    }
  }

  .sb-cp-standard-table_header_menu-badge {
    display: inline-block;
  }

  .sb-cp-standard-table_header_menu-hide-badge {
    display: none;
  }

  .sb-cp-standard-table_header_menu-icon {
    --sb-button-color: var(--sbt-theme-tertiary-color-20);
    --sb-button-color-contrast: var(--sbt-theme-tertiary-color-60);
    padding: var(--sbt-spacing-10);

    &:hover:not(&:disabled) {
      --sb-button-color: var(--sbt-theme-tertiary-color-40);
    }
  }
}

.sb-cp-standing-table_sorting-options {
  display: inline-block;
  width: 12px;
}

/* stylelint-disable selector-class-pattern -- Third Party Classes */
.sb-cp-standing-table_sorting-options .inactive {
  display: none;
}
