// SB ViewMore Variables:
// --sb-view-more-line-clamp

.sb-view-more {
  display: flex;
}

.sb-view-more__text {
  --sb-view-more-line-clamp: 4;
  /* stylelint-disable value-no-vendor-prefix */
  -webkit-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: var(--sb-view-more-line-clamp, 4);
  overflow: hidden;

  &:not(.sb-view-more__text_truncated) ~ .sb-view-more__expand {
    display: none;
  }
}

.sb-view-more__input {
  opacity: 0;
  pointer-events: none;
  position: absolute;

  &:focus ~ .sb-view-more__expand {
    outline: -webkit-focus-ring-color auto 5px;
  }

  &:checked + .sb-view-more__text {
    -webkit-line-clamp: unset;
  }

  &:checked ~ .sb-view-more__expand {
    display: none;
  }

  &:not(:checked) ~ .sb-view-more__collapse {
    display: none;
  }
}

.sb-view-more__action {
  align-self: flex-end;
  color: var(--sb-button-primary-color, var(--sbt-theme-color-70));
  cursor: pointer;
  flex-shrink: 0;

  &:hover {
    color: var(--sb-button-primary-color-dark, var(--sbt-theme-color-80));
  }

  &:focus {
    box-shadow: none;
  }
}

.sb-view-more__text_small,
.sb-view-more__action_small {
  font-size: var(--sb-typography-T10-font-size, var(--sbt-font-size-20));
  font-weight: var(--sb-typography-T10-font-weight, var(--sbt-font-weight-40));
  line-height: var(--sb-typography-T10-line-height, var(--sbt-line-height-30));
}

.sb-view-more__text_regular,
.sb-view-more__action_regular {
  font-size: var(--sb-typography-T15-font-size, var(--sbt-font-size-25));
  font-weight: var(--sb-typography-T15-font-weight, var(--sbt-font-weight-40));
  line-height: var(--sb-typography-T15-line-height, var(--sbt-line-height-40));
}

.sb-view-more__text_large,
.sb-view-more__action_large {
  font-size: var(--sb-typography-T20-font-size, var(--sbt-font-size-30));
  font-weight: var(--sb-typography-T20-font-weight, var(--sbt-font-weight-40));
  line-height: var(--sb-typography-T20-line-height, var(--sbt-line-height-50));
}
