.sb-cp-journey-summary-state-bullet {
  border: 1px solid transparent;
  border-radius: 50%;
  height: 8px;
  margin: 0 0 0 var(--sbt-spacing-20);
  width: 8px;
}

.sb-cp-journey-summary-state-bullet__active {
  background-color: var(--sbt-blue-70);
}

.sb-cp-journey-summary-state-bullet__canceled {
  background-color: var(--sbt-grey-70);
}

.sb-cp-journey-summary-state-bullet__completed {
  background-color: var(--sbt-green-70);
}

.sb-cp-journey-summary-state-bullet__at-risk {
  background-color: var(--sbt-red-70);
}

.sb-cp-journey-summary-state-item {
  align-items: center;
  border: 1px solid;
  border-radius: var(--sbt-border-radius-large);
  display: flex;
  gap: var(--sbt-spacing-30);
  padding: var(--sbt-spacing-10);
}

.sb-cp-journey-summary-state-item__active {
  border-color: var(--sbt-blue-70);
}

.sb-cp-journey-summary-state-item__canceled {
  border-color: var(--sbt-grey-70);
}

.sb-cp-journey-summary-state-item__completed {
  border-color: var(--sbt-green-70);
}

.sb-cp-journey-summary-state-item__at-risk {
  border-color: var(--sbt-red-70);
}
