// This copies the way the theme is generated in ag-grid-community, ensuring that we can add overrides to the alpine theme in the correct order every time. Otherwise, it would be subject to load order or specificity wars.
/* stylelint-disable order/properties-alphabetical-order, selector-class-pattern */
@use 'node_modules/ag-grid-community/styles' as ag;

@include ag.grid-styles(
  (
    /* We are extending and renaming the alpine them. `grid-styles` mixin will use the theme name set in `theme` param
    and prepend `ag-theme-` to it so in this case the final theme name is `ag-theme-sb-data-grid-theme`. */
      theme: sb-data-grid-theme,
    extend-theme: alpine
  )
);

.ag-theme-sb-data-grid-theme {
  font-family: var(--sbt-font-stack-primary);

  .ag-root-wrapper {
    border-bottom: var(
      --sb-table-bottom-border,
      var(--sb-table-cell-border, 1px solid)
    );
    border-left: var(
      --sb-table-left-border,
      var(--sb-table-cell-border, 1px solid)
    );
    border-right: var(
      --sb-table-right-border,
      var(--sb-table-cell-border, 1px solid)
    );
    border-top: var(
      --sb-table-top-border,
      var(--sb-table-cell-border, 1px solid)
    );
    border-color: var(--sb-table-color-border);
  }

  .ag-header {
    background-color: var(
      --sb-table-header-cell-color-background,
      var(--sbt-color-background),
      none
    );
    border-bottom: var(
      --sb-table-header-bottom-border,
      2px solid var(--sbt-theme-tertiary-color-50)
    );
  }

  .ag-header-cell,
  .ag-header-group-cell {
    color: var(--sb-table-header-color, var(--sbt-theme-color-90));
    font-size: var(--sb-table-header-cell-font-size);
    font-weight: var(
      --sb-table-header-cell-font-weight,
      var(--sbt-font-weight-70)
    );
    padding: var(--sb-table-header-cell-padding, 0.5rem);
    text-align: var(--sb-table-header-cell-text-align, 'left');
    white-space: nowrap;
  }

  .ag-row {
    border-bottom: var(
      --sb-table-row-bottom-border,
      var(--sb-table-row-border, 1px solid)
    );
    border-left: var(
      --sb-table-row-left-border,
      var(--sb-table-row-border, none)
    );
    border-right: var(
      --sb-table-row-right-border,
      var(--sb-table-row-border, none)
    );
    border-top: var(
      --sb-table-row-top-border,
      var(--sb-table-row-border, none)
    );
    border-color: var(--sb-table-color-border);
  }

  .ag-row-odd {
    background-color: var(
      --sb-table-striped-row-even-color-background,
      var(--sbt-theme-tertiary-color-10)
    );
  }

  .ag-cell.ag-cell {
    align-items: center;
    background-color: var(--sb-table-cell-background-color);
    border-bottom: var(
      --sb-table-cell-bottom-border,
      var(--sb-table-cell-border, 1px solid)
    );
    border-left: var(
      --sb-table-cell-left-border,
      var(--sb-table-cell-border, 1px solid)
    );
    border-right: var(
      --sb-table-cell-right-border,
      var(--sb-table-cell-border, 1px solid)
    );
    border-top: var(
      --sb-table-cell-top-border,
      var(--sb-table-cell-border, 1px solid)
    );
    border-color: var(--sb-table-color-border);
    display: flex;
    font-size: var(--sb-table-cell-font-size);
    height: 100%;
    line-height: var(--sb-table-cell-line-height, --sb-table-cell-font-size);
    padding: var(--sb-table-cell-padding, 0 0.5rem);
    padding-left: var(--sb-table-padding-cell-side);
    padding-right: var(--sb-table-padding-cell-side);
    white-space: nowrap;

    .ag-cell-wrapper {
      min-width: 0;
    }
  }

  // Disable styling from selecting individual cells
  .ag-cell-focus:not(.ag-cell-range-selected):focus-within {
    border-color: var(
      --sb-table-selected-highlight-outline,
      transparent
    ) !important;
    outline: var(--sb-table-selected-highlight-outline, initial) !important;
    border: 0 !important;
  }

  .ag-context-menu-open .ag-cell-focus:not(.ag-cell-range-selected) {
    border: 0 !important;
    border-color: var(
      --sb-table-selected-highlight-outline,
      transparent
    ) !important;
  }

  .ag-row-hover {
    background-color: var(--sb-table-row-hover, initial);
  }

  .ag-row-hover,
  .ag-row-focus {
    border-color: var(--sb-table-focus-color, var(--sbt-theme-color-10));
  }

  .ag-row-selected {
    // Copied from ag-grid to prevent stripe styles from winning
    background-color: var(
      --ag-selected-row-background-color,
      rgba(33, 150, 243, 0.3)
    );
  }

  .ag-row-selected::before {
    background-color: transparent;
  }

  .ag-header-cell-comp-wrapper {
    height: 100%;
  }
}
