.sb-datagrid_group-row {
  align-items: center;
  display: flex;
}

.sb-datagrid_group-row_label {
  margin-right: var(--sbt-spacing-40);
}

.sb-datagrid_group-row_count {
  background-color: var(
    --sb-data-grid-group-count-background-color,
    var(--sbt-theme-color-70)
  );
  color: var(--sb-data-grid-font-color, var(--sbt-color-text-invert));
  font-size: var(--sbt-font-size-20);
  font-weight: var(--sbt-font-weight-30);
}
