/* stylelint-disable selector-class-pattern -- Third Party Classes */
.sb-cp-standard-table {
  // Allow DateInput component inside DataGridDateFilter to render its popover menu outside
  // of the ag filter menu.
  .ag-tabs {
    overflow: visible;
  }

  .ag-tabs-header {
    display: none;
  }

  .ag-pinned-left-header,
  .ag-pinned-right-header {
    /* stylelint-disable-next-line */
    --ag-border-color: var(--sbt-theme-tertiary-color-20);
  }

  .ag-pinned-right-cols-container {
    box-shadow: -6px 4px 6px -6px rgb(0 0 0 / 15%);

    .ag-row {
      /* stylelint-disable-next-line */
      --ag-border-color: var(--sbt-theme-tertiary-color-20);
      border-left: 0;
      border-radius: 0;
    }

    .ag-cell {
      border-left: 0 !important;
    }
  }

  .ag-cell.sb-cp-options {
    padding: 0;
  }

  .sb-standard-table_primary-column {
    font-weight: var(--sbt-font-weight-50);
  }

  .ag-menu[aria-label='Column Menu'] {
    background-color: transparent;
    border: 0;
    box-shadow: none;
    padding-top: 10px;
    top: 39px !important;

    &::before {
      border-color: var(--sbt-theme-tertiary-color-30) transparent;
      border-style: solid;
      border-width: 0 10px 10px;
      content: '';
      display: block;
      left: 14px;
      position: absolute;
      top: 0;
      width: 0;
      z-index: 0;
    }

    &::after {
      border-color: #fff transparent;
      border-style: solid;
      border-width: 0 10px 10px;
      content: '';
      display: block;
      left: 14px;
      position: absolute;
      top: 1px;
      width: 0;
      z-index: 1;
    }
  }

  .ag-header-cell.ag-header-cell:first-child,
  .ag-cell.ag-cell:first-child {
    padding-left: 1.5rem;
  }

  &:not(.sb-cp-standard-table_options) .ag-cell.ag-cell:last-child,
  &:not(.sb-cp-standard-table_options)
    .ag-header-cell.ag-header-cell:last-child {
    padding-right: 1.5rem;
  }

  &.sb-cp-standard-table_options .ag-cell:last-child {
    padding-left: var(--sb-table-padding-cell-side);
    padding-right: 1.5rem;
  }

  .ag-pinned-right-header .ag-header-cell:first-child {
    padding-left: var(--sb-table-padding-cell-side);
    padding-right: calc(var(--sb-table-padding-cell-side) * 2);
  }

  &.sb-cp-standard-table_options-pinned .ag-cell.ag-cell:last-child {
    padding-right: var(--sb-table-padding-cell-side);

    &.sb-cp-options {
      padding-right: calc(var(--sb-table-padding-cell-side) * 2);
    }
  }

  &:not(.sb-cp-standard-table_options-pinned)
    .ag-header-cell.ag-header-cell:last-child {
    padding-right: calc(var(--sb-table-padding-cell-side) * 2);
  }

  .ag-header-cell {
    padding-left: var(--sb-table-padding-cell-side);
    padding-right: var(--sb-table-padding-cell-side);
  }
}

.sb-cp-standard-table_options-pinned .ag-row {
  border-bottom-right-radius: 0;
  border-right: 0;
  border-top-right-radius: 0;
}
