.sb-cp-nav-item {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  padding: var(--sbt-spacing-20) var(--sbt-spacing-20);

  &.sb-cp-nav-item__active-light {
    background: inherit;
    border-bottom: 4px solid var(--sbt-white);
    color: var(--sbt-white);
    font-weight: bold;
    padding: var(--sbt-spacing-20) var(--sbt-spacing-20) var(--sbt-spacing-10)
      var(--sbt-spacing-20);
    transition: all 0.2s ease-in-out;
  }

  &.sb-cp-nav-item__active-dark {
    background: inherit;
    border-bottom: 4px solid var(--sbt-gray-80);
    color: var(--sbt-gray-80);
    font-weight: bold;
    padding: var(--sbt-spacing-20) var(--sbt-spacing-20) var(--sbt-spacing-10)
      var(--sbt-spacing-20);
    transition: all 0.2s ease-in-out;
  }

  &:hover {
    cursor: pointer;
  }
}
