.sb-cp-action-area {
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: var(--sbt-spacing-10);
  justify-content: flex-end;

  &.sb-cp-action-area__area-light {
    --sb-button-primary-color: var(--sbt-white);
    color: var(--sbt-white);
  }

  &.sb-cp-action-area__area-dark {
    --sb-button-primary-color: var(--sbt-gray-80);
    color: var(--sbt-gray-80);
  }
}

.sb-cp-copilot {
  align-items: center;
  display: inline-flex;
}

.sb-cp-action-item {
  background: inherit;
  border: none;
  color: inherit;

  &:hover {
    cursor: pointer;
  }
}

.sb-cp-action-divider {
  border-left: 1px solid;
  height: 80%;
  margin: 0 var(--sbt-spacing-20);
}

.sb-cp-top-nav-template {
  display: grid;
  gap: var(--sbt-spacing-10);
  grid-template-columns: 1fr 3fr 1fr;
  height: calc(var(--sbt-base-unit) * 16);
  padding: 0 var(--sbt-spacing-20);

  // This will be replaced when we get more direction from product
  // on the colors for each domain
  &.sb-cp-top-nav-template__default {
    background-color: #47a18a;
  }

  &.sb-cp-top-nav-template__missioncontrol {
    background-color: var(--sbt-teal-50);
  }

  &.sb-cp-top-nav-template__admin {
    background-color: var(--sbt-red-50);
  }

  &.sb-cp-top-nav-template__incubation {
    background-color: var(--sbt-orange-50);
  }
}

.sb-cp-nav-title {
  align-items: center;
  display: flex;
  font-size: var(--sbt-font-size-40);
  font-weight: var(--sbt-font-weight-60);
  padding: 0 0 0 var(--sbt-spacing-30);

  &.sb-cp-nav-title__font-light {
    color: var(--sbt-white);
  }

  &.sb-cp-nav-title__font-dark {
    color: var(--sbt-gray-80);
  }
}

.sb-cp-nav-items-row {
  display: flex;
  flex-direction: row;
  gap: var(--sbt-spacing-20);
  justify-content: flex-start;
  margin: 0 var(--sbt-spacing-20);
  overflow-x: scroll;
  position: static;
  scrollbar-width: none;
  width: 100%;

  &::-webkit-scrollbar {
    display: none;
  }

  &.sb-cp-nav-items-row__gradient-light {
    color: var(--sbt-gray-20);
    -webkit-mask: linear-gradient(to right, #000 10% 80%, #0000);
    mask: linear-gradient(to right, #000 10% 80%, #0000);

    &::after {
      content: '';
      padding-right: calc(var(--sbt-base-unit) * 24);
    }
  }

  &.sb-cp-nav-items-row__gradient-dark {
    color: var(--sbt-gray-80);
    -webkit-mask: linear-gradient(to right, #000 10% 80%, #0000);
    mask: linear-gradient(to right, #000 10% 80%, #0000);

    &::after {
      content: '';
      padding-right: calc(var(--sbt-base-unit) * 24);
    }
  }
}
